import { Modal, ModalBackground, ModalContent, 
    ModalClose, Box, ModalCard, ModalCardBody, Button,
    ModalCardHeader, ModalCardTitle, ModalCardFooter, Delete
} from 'bloomer';

export default function ModalContainer({ active, close, children, 
    colored, isCard, title }) {
    const style = { 
        color: {
            backgroundColor: '#00d1b2' 
        },
        size: {
            width: isCard ? '40vw' : '60vw'
        }
    }

    if (isCard) {
        return (
            <Modal isActive={active}>
                <ModalBackground onClick={close}  />
                <ModalCard style={style.size}>
                    <ModalCardHeader>
                        <ModalCardTitle>{title}</ModalCardTitle>
                        <Delete onClick={close}  />
                    </ModalCardHeader>
                    <ModalCardBody>
                        {children}
                    </ModalCardBody>
                    <ModalCardFooter>
                        <Button isColor='primary'>Sim</Button>
                        <Button isColor='danger'>Cancelar</Button>
                    </ModalCardFooter>
                </ModalCard>
            </Modal>
        );
    }

    return (
        <Modal isActive={active}>
            <ModalBackground style={ colored && style.color } onClick={close} />
            <ModalContent style={style.size}>
                <Box >
                    {children}
                </Box>
            </ModalContent>
            <ModalClose onClick={close} />
        </Modal>
    )
}